import React, { useState } from 'react'
import { db } from '../Firebase';
import { useSelector } from 'react-redux'
import './style.css'
import firebase from 'firebase';

const CreateComment = (props) => {
    const timestamp = firebase.firestore.FieldValue.serverTimestamp;
    const [comment, setComment] = useState('');
    const user = useSelector(state => state.userReducer.user[0]);

    const handleChange=(e)=>{
        setComment(e.target.value);
    }
    const handleComment=()=>{
        if(comment!==''){
        db.collection("Posts").doc(props.id).collection("Comments").add({
            Name:user.name,
            ProfileUrl:user.pic,
            Comment:comment,
            createdAt: timestamp()
        });
        setComment('');
        }
    }

    return (
        <div className="createComment">
            <textarea placeholder='Comment here...' id='createCommentText' value={comment} onChange={handleChange}></textarea>
            <div id='btns'>
            <button type="button" style={{cursor:"pointer"}} onClick={handleComment} id="upload">Comment</button>
            </div>
        </div>
    )
}

export default CreateComment;
