import React from 'react'
import Logo from '../Components/Logo'
import SignInButton from '../Components/SignInButton'
import './style.css'
import { useSelector } from 'react-redux'

const Navbar = () => {
    //const [user, setUser] = useState()
    const user = useSelector(state => state.userReducer.user[0]);
    function toggleDrop(){
        var drop=document.getElementById("dropDown");
        if(drop.style.display==="block")
            drop.style.display="none";
        else
            drop.style.display="block";
    }
    return (
        
        <div className="navbar">
            <Logo/>
            {(user)?<div className="proImg btn" onClick={toggleDrop}><img alt="img" id="nav-pic" src={user.pic}></img>
            <div className="dropDown btn" id="dropDown" onClick={() => window.location.reload(false)}><p>Log Out</p></div>
            </div>:<SignInButton/>}
        </div>
    )
}

export default Navbar
