import React,{useState} from 'react'
import './style.css'
import SignInButton from '../Components/SignInButton'
import { useSelector } from 'react-redux'
import { db, storage } from '../Firebase';
import firebase from 'firebase';

function randomString() {  
            //define a variable consisting alphabets in small and capital letter  
        var characters = "ABCDEFGHIJKLMNOPQRSTUVWXTZabcdefghiklmnopqrstuvwxyz1234567890";  
            
            //specify the length for the new string  
        var lenString = 7;  
        var randomstring = '';  

            //loop to select a new character in each iteration  
        for (var i=0; i<lenString; i++) {  
        var rnum = Math.floor(Math.random() * characters.length);  
        randomstring += characters.substring(rnum, rnum+1);  
        } 
        return randomstring;
} 

const CreatePost = () => {
    const timestamp = firebase.firestore.FieldValue.serverTimestamp;
    const user = useSelector(state => state.userReducer.user[0]);
    const [caption, setCaption] = useState('');
    const [img, setImg] = useState(null);
    const [progress, setProgress] = useState(0);

    const handleImg=(e)=>{
        setImg(e.target.files[0]);
        let imgURL=URL.createObjectURL(e.target.files[0]);
        let uploadedImg=document.getElementById("uploadedImg");
        uploadedImg.src=imgURL;
        uploadedImg.style.display="block";
    }
    const handleChange=(e)=>{
        setCaption(e.target.value);
    }
    const handleUpload=()=>{
        if(img){
            var imgId=randomString();
            const Upload=storage.ref(`Images/${imgId}.jpg`).put(img);

            Upload.on("state_changed",(snapshot)=>{
                const pro=Math.round((snapshot.bytesTransferred/snapshot.totalBytes)*100);
                setProgress(pro);
            }
            ,(error)=>{
                console.log(error)
            },
            ()=>{
                storage
                .ref('Images')
                .child(`${imgId}.jpg`)
                .getDownloadURL()
                .then((imgUrl)=>{
                    db.collection("Posts").add({
                        Name:user.name,
                        ProfileUrl:user.pic,
                        Email:user.email,
                        PhotoUrl:imgUrl,
                        Caption:caption,
                        createdAt: timestamp()
                    });
                });
            });
        }
        else{
            var imgId=randomString();
                    db.collection("Posts").add({
                        Name:user.name,
                        ProfileUrl:user.pic,
                        Email:user.email,
                        PhotoUrl:null,
                        Caption:caption,
                        createdAt: timestamp()
                    });
        }
        setCaption('');
        let uploadedImg=document.getElementById("uploadedImg");
        uploadedImg.style.display="none";
    }

    

    if(user){
    return (
        <div className='createPost'>
            <div className='createPostUpload'>
            <p>Create a post</p>
            <textarea placeholder='Add a Caption...' id='createPostText' value={caption} onChange={handleChange}></textarea>
            <div className="img"><img alt="error" id="uploadedImg"></img></div>
            <div id='btns'>
            <label htmlFor='photoUpload' className="btn" id='photoLabel'>Add Photo</label>
            <input type='file' id="photoUpload" onChange={handleImg}></input>
            <button type="button" className="btn" onClick={handleUpload} id="upload">UPLOAD</button>
            </div>
            </div>
        </div>
    )
    }else{
        return (
            <div className='createPostLogin'>
                <SignInButton/>
                <p>LogIn to create post</p>
            </div>
        )
    }
}

export default CreatePost
