import React,{useState,useEffect} from 'react'
import './style.css'
import { db } from '../Firebase'
import Comments from '../Components/Comments'
import CreateComment from '../Components/CreateComment'
import { useSelector } from 'react-redux'
import UpdateCaption from '../Components/UpdateCaption'

const Post = () => {
    useEffect(() => {
        getData();
    }, [])


    const [posts, setPosts] = useState([]);
    const user = useSelector(state => state.userReducer.user[0]);
    
    function getData(){db.collection("Posts").orderBy('createdAt').onSnapshot(
        function (querySnapshot) {
            setPosts(
            querySnapshot.docs.map((doc)=>({
                Name:doc.data().Name,
                Caption:doc.data().Caption,
                Email:doc.data().Email,
                PhotoUrl:doc.data().PhotoUrl,
                ProfileUrl:doc.data().ProfileUrl,
                id:doc.id
            })     
            ))
        }
    )}

    const deletePost=(id)=>{
        db.collection("Posts").doc(id).delete().then(() => {
            console.log("Document successfully deleted!");
        }).catch((error) => {
            console.error("Error removing document: ", error);
        });
    }

    const [toggleUpdate, setToggleUpdate] = useState(false)

    return (
        <div>
           {
               posts.map((post)=>{
                   return(
                       <div className='eachPost'>
                           <div id='post'>
                           <div id="postHead">
                               <div className='postHead'>
                               <img src={post.ProfileUrl}></img>
                               <p>{post.Name}</p>
                               </div>
                               {
                                   (user)?(user.email===post.Email)?<button type='button' id='deleteBtn' className="btn" onClick={()=>deletePost(post.id)}>Delete</button>:<></>:<></> 
                               }
                               
                           </div>
                           <div className="postBody">
                               <div id='captionBody'><p>{post.Caption}</p> 
                               {
                                   (user)?(user.email===post.Email)?<button type="button" id="editBtn" className="btn" onClick={()=>{(toggleUpdate)?setToggleUpdate(false):setToggleUpdate(true)}}>Edit</button>:<></>:<></>
                               }
                               </div>
                               {
                                    (user)?(user.email===post.Email)?toggleUpdate?<UpdateCaption id={post.id} caption={post.Caption}/>:<></>:<></>:<></>
                               } 
                               <div style={{width:"80vw",maxHeight:"60vw",overflow:"hidden",display:"flex",justifyContent:"center",alignItems:"center",paddingLeft:"3.5vw"}} id="postBodyImg">
                                <img src={post.PhotoUrl}></img>
                               </div> 
                           </div>
                           {(user)?<CreateComment id={post.id} posts={posts}/>:<></>}
                           <Comments id={post.id}/>
                           </div>
                       </div>
                   )
               })
           }
        </div>
    )
}

export default Post
