import React,{useState} from 'react'
import { db } from '../Firebase';

const UpdateCaption = (props) => {
    const [caption, setCaption] = useState(props.caption);
    const handleChange=(e)=>{
        setCaption(e.target.value);
    }
    const handleUpdate=(id)=>{
        db.collection("Posts").doc(id).update({Caption: caption});
    }
    return (
        <div className="updateCaption">
            <textarea placeholder='Add a Caption...' id='updateCaptionText' value={caption} onChange={handleChange}></textarea>
            <button type="button" className="update btn" onClick={()=>handleUpdate(props.id)}>Update</button>
        </div>
    )
}

export default UpdateCaption
